import Link from 'react-router/lib/Link'
import {Card,Row,Col,Radio,Form,Alert} from 'antd'

import {WeaTop} from 'weaCom'

import * as API from '../api/util'

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const FormItem = Form.Item;

class Home extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		return (
			<div>
			<WeaTop title="Ecology"></WeaTop>
			<div className="code-box-demo" style={{ background: '#ECECEC', padding: '30px' }}>
				<Row>
					<Col span="12">
						<Card title="" bordered={false}>
							<Link to="/export"></Link>
						</Card>
					</Col>
					<Col span="12">
						<Card title="" bordered={false}>
							<Link to="/import"></Link>
						</Card>
					</Col>
				</Row>
			</div>
			</div>
		)
	}
}

export default Home


